.formBlock__text {
  font-size: 20px;
  color: #9a9a9a;
  text-align: center;
}
@media only screen and (min-width: 992px) {
  .formBlock__text {
    text-align: inherit;
  }
}
.formBlock__text strong {
  display: block;
  color: #000;
  font-weight: 900;
}
@media only screen and (min-width: 992px) {
  .formBlock__text strong {
    display: inline;
  }
}
.formBlock__line {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  margin: 55px 0;
}
@media only screen and (min-width: 992px) {
  .formBlock__line {
    flex-direction: row;
    align-items: inherit;
    justify-content: space-between;
  }
}
@media only screen and (min-width: 1200px) {
  .formBlock__line {
    margin-bottom: 110px;
  }
}
.formBlock .inputWrap {
  position: relative;
}
.formBlock span {
  position: absolute;
  top: -10px;
  left: 10px;
  padding-left: 10px;
  padding-right: 10px;
  background: #f7faf9;
  font-size: 12px;
  font-weight: 400;
}
.formBlock label {
  position: absolute;
  bottom: -10px;
  left: 10px;
  padding-left: 10px;
  padding-right: 10px;
  background: #f7faf9;
  font-size: 12px;
  font-weight: 400;
  color: red;
}
.formBlock .form__line {
  padding: 18px 20px;
  margin-bottom: 35px;
  width: 290px;
  background: #f7faf9;
  border: 1px solid #c7c7c7;
}
@media only screen and (min-width: 992px) {
  .formBlock .form__line {
    width: 310px;
    margin-bottom: 0;
  }
}
@media only screen and (min-width: 1200px) {
  .formBlock .form__line {
    width: 410px;
  }
}
.formBlock .form__line:focus:invalid {
  border: solid 1px #f15b4c;
  outline: none;
}
.formBlock .form__line:valid {
  border: solid 1px #a6d255;
  outline: none;
}
.formBlock .form__line-phone {
  padding: 18px 20px;
  width: 290px;
  background: #f7faf9;
  border: 1px solid #c7c7c7;
}
@media only screen and (min-width: 992px) {
  .formBlock .form__line-phone {
    width: 280px;
  }
}
.formBlock .form__line-phone:focus:invalid {
  border: solid 1px #f15b4c;
  outline: none;
}
.formBlock .form__line-phone:valid {
  border: solid 1px #a6d255;
  outline: none;
}
.formBlock .btn {
  display: table;
  margin: auto;
  padding: 8px 0;
  width: 280px;
  background: none;
  border: 2px solid #f15b4c;
  font-weight: 700;
  font-size: 18px;
  text-transform: uppercase;
  border-radius: 20px;
  transition: all .3s linear;
}
.formBlock .btn:hover {
  background: #f15b4c;
  transform: scale(0.97);
}
.formPick {
  width: 260px;
  padding: 2px 20px;
  background: #f7faf9;
  border: 1px solid #c7c7c7;
}
@media only screen and (min-width: 768px) {
  .formPick {
    width: inherit;
  }
}
.datepicker-container {
  width: 255px;
  font-family: 'Geometria', sans-serif;
}
@media only screen and (min-width: 768px) {
  .datepicker-container {
    width: 280px;
  }
}
.datepicker-top-left:after,
.datepicker-top-left:before,
.datepicker-top-right:after,
.datepicker-top-right:before {
  left: 50%;
  transform: translateX(-50%);
}
.datepicker-panel > ul > li {
  height: 35px;
  width: 35px;
  font-size: 17px;
  font-family: 'Geometria', sans-serif;
}
@media only screen and (min-width: 768px) {
  .datepicker-panel > ul > li {
    height: 40px;
    width: 40px;
    font-size: 18px;
  }
}
.datepicker-panel > ul > li[data-view="month current"],
.datepicker-panel > ul > li[data-view="year current"],
.datepicker-panel > ul > li[data-view="years current"] {
  width: 180px;
}
@media only screen and (min-width: 768px) {
  .datepicker-panel > ul > li[data-view="month current"],
  .datepicker-panel > ul > li[data-view="year current"],
  .datepicker-panel > ul > li[data-view="years current"] {
    width: 200px;
  }
}
